.compare {

  &__card {
    padding: 0 15px 15px;

    &:hover {
      transform: scale(1.05);
      box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
    }

    &__card:not(:last-child) {
      margin-bottom: 40px;

      @include onTablet {
        margin-bottom: 48px;
      }

      @include onDesktop {
        margin-bottom: 0;
      }
    }

    &__image {
      display: block;
      max-width: 100%;
      margin: auto;
      margin-bottom: 32px;
    }

    &__subtitle,
    &__price {
      font-weight: 700;
      font-size: 24px;
    }

    &__subtitle {
      margin-bottom: 8px;
    }

    &__description {
      margin-bottom: 16px;
    }
  }
}
